import React from 'react';
import Navbar from '../../Components/Navbar';

import { ContainerHabilidades, StyledHabilidades } from './Habilidades';
import Card from '../../Components/Card'

import HabilidadesArray from '../../Data/Habilidades';
import { Link } from 'react-router-dom';


const Habilidades = () => {
  return (
    <>
      <Navbar background = {`linear-gradient(rgba(5,7,12,0.75), rgba(5,7,12,0.75))`}  color = {'white'}/>
      <StyledHabilidades>
        <ContainerHabilidades>
          {
            HabilidadesArray.map((el, key) => {
            return(
              <Link to={`/habilidades/${el.nombre}`} className='link'>
                <Card nombre = {el.nombre} foto = {el.foto} />              
              </Link>
            )
            })
          }
        </ContainerHabilidades>
      </StyledHabilidades>
    </>
  );
}

export default Habilidades;
