import React from 'react';
import { useParams } from 'react-router-dom';
import Navbar from '../../Components/Navbar';
import HabilidadesArray from '../../Data/Habilidades';
import Users from '../../Data/Users';
import { Container, ContainerHabilidad, ContainerUsuarios } from './Habilidad';
import Card from '../../Components/Card';
import CardUser from '../../Components/CardUser';

const PerfilHabilidad = () => {
  const { id } = useParams();
  let contador = 0;
  const filteredHabilidades = HabilidadesArray.filter((el) => el.nombre === id);
  const filteredUsuarios = Users.filter((el) => {
    contador = 0;
    el.habilidades.forEach((habilidad) => {
      if (habilidad === id) {
        contador++;
      }
    });
    return contador > 0;
  });

  console.log(filteredUsuarios);

  return (
    <>
      <Navbar color="black" />
      <Container>
        <ContainerHabilidad>
          {filteredHabilidades.map((el) => (
            <Card key={el.id} nombre={el.nombre} foto={el.foto} />
          ))}
        </ContainerHabilidad>
        <ContainerUsuarios>
          {
            filteredUsuarios.map((el) => (
              <CardUser id = {el.id} nombre = {el.nombre} apellido = {el.apellido} habilidades = {el.habilidades} edad = {el.edad} foto = {el.foto}/>
            ))
          }
        </ContainerUsuarios>
      </Container>
    </>
  );
};

export default PerfilHabilidad;