import React from 'react';
import {ContainerDatos, ContainerHabilidades, ContainerCard, EtiquetaHabilidad, StyledImg } from './CardUser';
import HabilidadesArray from '../../Data/Habilidades';
import { Link } from 'react-router-dom';

const CardUser = (props) => {
  let contador = 0; 
  const filteredHabilidades = HabilidadesArray.filter((habilidad) =>
    props.habilidades.includes(habilidad.nombre)
  );
  console.log(filteredHabilidades);
  return (
    <>      
      <Link to = {`/usuario/${props.id}`} style={{
        textDecoration:'none',
        color:'black'
      }}>

        <ContainerCard>
          <StyledImg src={props.foto}></StyledImg>
          <ContainerDatos>
          <p>{props.nombre} {props.apellido}</p>
          <ContainerHabilidades>
              {filteredHabilidades.map((el) => (
                <EtiquetaHabilidad background = {el.color}>{el.nombre}</EtiquetaHabilidad>
              ))}
          </ContainerHabilidades>
          </ContainerDatos>
        </ContainerCard>
      </Link>
    </>
  );
}

export default CardUser;
